import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query'
import { reportsService, Rapport, RapportCreate, RapportPlanifie, RapportPlanifieCreate } from '@/lib/api/reports.service'
import { toast } from 'sonner'

// ===== Hooks pour les Rapports =====

export function useReports(equipeId?: string, filters?: { type?: string; statut?: string }) {
  return useQuery({
    queryKey: ['reports', equipeId, filters],
    queryFn: () => reportsService.getReports({ equipe: equipeId, ...filters }),
    enabled: !!equipeId,
  })
}

export function useReport(id?: string) {
  return useQuery({
    queryKey: ['reports', id],
    queryFn: () => reportsService.getReport(id!),
    enabled: !!id,
  })
}

export function useCreateReport() {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (data: RapportCreate) => reportsService.createReport(data),
    onSuccess: (newReport) => {
      queryClient.invalidateQueries({ queryKey: ['reports'] })
      toast.success('Rapport en cours de génération', {
        description: 'Vous serez notifié lorsqu\'il sera prêt',
      })
    },
    onError: (error: any) => {
      console.error('Erreur création rapport:', error.response?.data)
      
      // Afficher les erreurs de validation détaillées
      const errorData = error.response?.data
      let errorMessage = error.message
      
      if (errorData && typeof errorData === 'object') {
        // Si c'est un objet d'erreurs de validation
        const errors = Object.entries(errorData)
          .map(([field, msgs]) => {
            const messages = Array.isArray(msgs) ? msgs : [msgs]
            return `${field}: ${messages.join(', ')}`
          })
          .join('\n')
        errorMessage = errors || JSON.stringify(errorData)
      }
      
      toast.error('Erreur lors de la création du rapport', {
        description: errorMessage,
      })
    },
  })
}

export function useDeleteReport() {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (id: string) => reportsService.deleteReport(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['reports'] })
      toast.success('Rapport supprimé')
    },
    onError: (error: any) => {
      toast.error('Erreur lors de la suppression', {
        description: error.response?.data?.error || error.message,
      })
    },
  })
}

export function useDownloadReport() {
  return useMutation({
    mutationFn: (id: string) => reportsService.downloadReport(id),
    onSuccess: (data) => {
      // Ouvrir le téléchargement dans un nouvel onglet
      window.open(data.url, '_blank')
      toast.success('Téléchargement démarré')
    },
    onError: (error: any) => {
      toast.error('Erreur lors du téléchargement', {
        description: error.response?.data?.error || error.message,
      })
    },
  })
}

export function useRegenerateReport() {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (id: string) => reportsService.regenerateReport(id),
    onSuccess: (report) => {
      queryClient.invalidateQueries({ queryKey: ['reports'] })
      queryClient.invalidateQueries({ queryKey: ['reports', report.id] })
      toast.success('Régénération du rapport lancée')
    },
    onError: (error: any) => {
      toast.error('Erreur lors de la régénération', {
        description: error.response?.data?.error || error.message,
      })
    },
  })
}

export function useReportStatistics(equipeId?: string) {
  return useQuery({
    queryKey: ['reports', 'statistics', equipeId],
    queryFn: () => reportsService.getStatistiques(equipeId!),
    enabled: !!equipeId,
  })
}

