'use client'

import { useState } from 'react'
import { useTeamStore } from '@/lib/store/team-store'
import { useTeamMembers } from '@/lib/hooks/use-teams'
import { useCreateReport } from '@/lib/hooks/use-reports'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Label } from '@/components/ui/label'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { Calendar } from '@/components/ui/calendar'
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Checkbox } from '@/components/ui/checkbox'
import { Badge } from '@/components/ui/badge'
import {
  FileText,
  Download,
  Calendar as CalendarIcon,
  Users,
  User,
  TrendingUp,
  AlertCircle,
  FileSpreadsheet,
  Loader2,
  CheckCircle2,
} from 'lucide-react'
import { format } from 'date-fns'
import { fr } from 'date-fns/locale'
import { cn } from '@/lib/utils'
import { toast } from 'sonner'

type ReportType = 'individual' | 'team' | 'weekly' | 'monthly'
type ReportPeriod = '7days' | '14days' | '30days' | '90days' | 'custom'
type ReportFormat = 'pdf' | 'excel'

interface ReportConfig {
  type: ReportType
  period: ReportPeriod
  format: ReportFormat
  playerId?: string
  startDate?: Date
  endDate?: Date
  sections: {
    overview: boolean
    statistics: boolean
    charts: boolean
    alerts: boolean
    wellness: boolean
    sessions: boolean
  }
}

export default function ReportsPage() {
  const { currentTeam } = useTeamStore()
  const { data: membersData, isLoading: isLoadingPlayers } = useTeamMembers(currentTeam?.id || '')
  const { mutate: createReport, isPending: isGenerating } = useCreateReport()
  
  // Filtrer uniquement les joueurs (role PLAYER)
  const players = membersData?.membres?.filter(m => m.role === 'PLAYER') || []
  
  const [config, setConfig] = useState<ReportConfig>({
    type: 'team',
    period: '30days',
    format: 'pdf',
    sections: {
      overview: true,
      statistics: true,
      charts: true,
      alerts: true,
      wellness: true,
      sessions: true,
    },
  })
  
  const [dateRange, setDateRange] = useState<{ from?: Date; to?: Date }>({})

  const handleGenerateReport = async () => {
    if (!currentTeam?.id) {
      toast.error('Veuillez sélectionner une équipe')
      return
    }

    if (config.type === 'individual' && !config.playerId) {
      toast.error('Veuillez sélectionner un joueur')
      return
    }

    if (config.period === 'custom' && (!dateRange.from || !dateRange.to)) {
      toast.error('Veuillez sélectionner une période personnalisée')
      return
    }

    // Mapping des types frontend vers backend
    const typeMapping: Record<string, string> = {
      'individual': 'INDIVIDUEL',
      'team': 'EQUIPE',
      'weekly': 'HEBDOMADAIRE',
      'monthly': 'MENSUEL',
    }

    // Préparer les données pour l'API
    const reportData: any = {
      equipe: currentTeam.id,
      type: typeMapping[config.type] || config.type.toUpperCase(),
      format: config.format.toUpperCase(),
      sections: config.sections,
    }

    // Ajouter le joueur UNIQUEMENT pour les rapports individuels
    if (config.type === 'individual') {
      if (!config.playerId) {
        toast.error('Veuillez sélectionner un joueur')
        return
      }
      reportData.joueur = config.playerId
    }

    // Ajouter la période
    if (config.period === 'custom' && dateRange.from && dateRange.to) {
      reportData.dateDebut = dateRange.from.toISOString().split('T')[0]
      reportData.dateFin = dateRange.to.toISOString().split('T')[0]
    } else {
      reportData.periode = config.period
    }

    createReport(reportData)
  }


  const getPeriodLabel = (period: ReportPeriod) => {
    switch (period) {
      case '7days':
        return '7 derniers jours'
      case '14days':
        return '14 derniers jours'
      case '30days':
        return '30 derniers jours'
      case '90days':
        return '90 derniers jours'
      case 'custom':
        return 'Période personnalisée'
    }
  }

  const getReportTypeIcon = (type: ReportType) => {
    switch (type) {
      case 'individual':
        return <User className="h-5 w-5" />
      case 'team':
        return <Users className="h-5 w-5" />
      case 'weekly':
        return <CalendarIcon className="h-5 w-5" />
      case 'monthly':
        return <TrendingUp className="h-5 w-5" />
    }
  }

  const reportTypes = [
    {
      value: 'individual' as ReportType,
      label: 'Rapport Individuel',
      description: 'Analyse détaillée d\'un joueur',
      icon: <User className="h-8 w-8" />,
    },
    {
      value: 'team' as ReportType,
      label: 'Rapport d\'Équipe',
      description: 'Vue d\'ensemble de toute l\'équipe',
      icon: <Users className="h-8 w-8" />,
    },
    {
      value: 'weekly' as ReportType,
      label: 'Rapport Hebdomadaire',
      description: 'Résumé de la semaine écoulée',
      icon: <CalendarIcon className="h-8 w-8" />,
    },
    {
      value: 'monthly' as ReportType,
      label: 'Rapport Mensuel',
      description: 'Analyse du mois complet',
      icon: <TrendingUp className="h-8 w-8" />,
    },
  ]

  return (
    <div className="p-4 sm:p-6 lg:p-8 space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl sm:text-3xl font-bold text-slate-900">Rapports</h1>
        <p className="text-sm sm:text-base text-slate-600 mt-1">
          Générez et téléchargez des rapports d'analyse
        </p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Configuration */}
        <div className="lg:col-span-2 space-y-6">
          {/* Type de Rapport */}
          <Card>
            <CardHeader>
              <CardTitle>Type de Rapport</CardTitle>
              <CardDescription>
                Sélectionnez le type de rapport à générer
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                {reportTypes.map((type) => (
                  <button
                    key={type.value}
                    onClick={() => setConfig({ ...config, type: type.value })}
                    className={cn(
                      'relative flex flex-col items-center p-6 rounded-lg border-2 transition-all hover:border-primary/50',
                      config.type === type.value
                        ? 'border-primary bg-primary/5'
                        : 'border-slate-200 bg-white'
                    )}
                  >
                    <div className={cn(
                      'mb-3',
                      config.type === type.value ? 'text-primary' : 'text-slate-400'
                    )}>
                      {type.icon}
                    </div>
                    <h3 className="font-semibold text-sm text-center mb-1">
                      {type.label}
                    </h3>
                    <p className="text-xs text-slate-500 text-center">
                      {type.description}
                    </p>
                    {config.type === type.value && (
                      <div className="absolute top-2 right-2">
                        <CheckCircle2 className="h-5 w-5 text-primary" />
                      </div>
                    )}
                  </button>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Sélection Joueur (si rapport individuel) */}
          {config.type === 'individual' && (
            <Card>
              <CardHeader>
                <CardTitle>Sélection du Joueur</CardTitle>
                <CardDescription>
                  Choisissez le joueur pour le rapport
                </CardDescription>
              </CardHeader>
              <CardContent>
                {isLoadingPlayers ? (
                  <div className="flex items-center justify-center py-8">
                    <Loader2 className="h-6 w-6 animate-spin text-primary" />
                  </div>
                ) : (
                  <Select
                    value={config.playerId}
                    onValueChange={(value) => setConfig({ ...config, playerId: value })}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Sélectionner un joueur" />
                    </SelectTrigger>
                    <SelectContent>
                      {players?.map((player) => (
                        <SelectItem key={player.id} value={player.id}>
                          <div className="flex items-center gap-2">
                            <Badge variant="outline">{player.profil_joueur?.dossard || '--'}</Badge>
                            <span>{player.nom}</span>
                          </div>
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                )}
              </CardContent>
            </Card>
          )}

          {/* Période */}
          <Card>
            <CardHeader>
              <CardTitle>Période</CardTitle>
              <CardDescription>
                Définissez la période d'analyse
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 sm:grid-cols-3 gap-3">
                {(['7days', '14days', '30days', '90days', 'custom'] as ReportPeriod[]).map((period) => (
                  <Button
                    key={period}
                    variant={config.period === period ? 'default' : 'outline'}
                    onClick={() => setConfig({ ...config, period })}
                    className="justify-start"
                  >
                    {getPeriodLabel(period)}
                  </Button>
                ))}
              </div>

              {config.period === 'custom' && (
                <div className="space-y-4 pt-4 border-t">
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label>Date de début</Label>
                      <Popover>
                        <PopoverTrigger asChild>
                          <Button
                            variant="outline"
                            className={cn(
                              'w-full justify-start text-left font-normal',
                              !dateRange.from && 'text-muted-foreground'
                            )}
                          >
                            <CalendarIcon className="mr-2 h-4 w-4" />
                            {dateRange.from ? (
                              format(dateRange.from, 'PPP', { locale: fr })
                            ) : (
                              <span>Sélectionner</span>
                            )}
                          </Button>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0">
                          <Calendar
                            mode="single"
                            selected={dateRange.from}
                            onSelect={(date) => setDateRange({ ...dateRange, from: date })}
                            initialFocus
                          />
                        </PopoverContent>
                      </Popover>
                    </div>

                    <div className="space-y-2">
                      <Label>Date de fin</Label>
                      <Popover>
                        <PopoverTrigger asChild>
                          <Button
                            variant="outline"
                            className={cn(
                              'w-full justify-start text-left font-normal',
                              !dateRange.to && 'text-muted-foreground'
                            )}
                          >
                            <CalendarIcon className="mr-2 h-4 w-4" />
                            {dateRange.to ? (
                              format(dateRange.to, 'PPP', { locale: fr })
                            ) : (
                              <span>Sélectionner</span>
                            )}
                          </Button>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0">
                          <Calendar
                            mode="single"
                            selected={dateRange.to}
                            onSelect={(date) => setDateRange({ ...dateRange, to: date })}
                            initialFocus
                          />
                        </PopoverContent>
                      </Popover>
                    </div>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>

          {/* Contenu du Rapport */}
          <Card>
            <CardHeader>
              <CardTitle>Contenu du Rapport</CardTitle>
              <CardDescription>
                Sélectionnez les sections à inclure
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {Object.entries({
                  overview: 'Vue d\'ensemble',
                  statistics: 'Statistiques détaillées',
                  charts: 'Graphiques d\'évolution',
                  alerts: 'Historique des alertes',
                  wellness: 'Indice de forme',
                  sessions: 'Historique des séances',
                }).map(([key, label]) => (
                  <div key={key} className="flex items-center space-x-2">
                    <Checkbox
                      id={key}
                      checked={config.sections[key as keyof typeof config.sections]}
                      onCheckedChange={(checked) =>
                        setConfig({
                          ...config,
                          sections: {
                            ...config.sections,
                            [key]: checked,
                          },
                        })
                      }
                    />
                    <label
                      htmlFor={key}
                      className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70 cursor-pointer"
                    >
                      {label}
                    </label>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Résumé et Actions */}
        <div className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle>Résumé</CardTitle>
              <CardDescription>
                Configuration du rapport
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-3">
                <div className="flex items-start gap-3">
                  <div className="text-primary mt-0.5">
                    {getReportTypeIcon(config.type)}
                  </div>
                  <div className="flex-1">
                    <p className="text-sm font-medium">Type</p>
                    <p className="text-sm text-slate-600">
                      {reportTypes.find((t) => t.value === config.type)?.label}
                    </p>
                  </div>
                </div>

                {config.type === 'individual' && config.playerId && (
                  <div className="flex items-start gap-3">
                    <User className="h-5 w-5 text-primary mt-0.5" />
                    <div className="flex-1">
                      <p className="text-sm font-medium">Joueur</p>
                      <p className="text-sm text-slate-600">
                        {players?.find((p) => p.id === config.playerId)?.nom || 'Non sélectionné'}
                      </p>
                    </div>
                  </div>
                )}

                <div className="flex items-start gap-3">
                  <CalendarIcon className="h-5 w-5 text-primary mt-0.5" />
                  <div className="flex-1">
                    <p className="text-sm font-medium">Période</p>
                    <p className="text-sm text-slate-600">
                      {getPeriodLabel(config.period)}
                    </p>
                    {config.period === 'custom' && dateRange.from && dateRange.to && (
                      <p className="text-xs text-slate-500 mt-1">
                        {format(dateRange.from, 'dd/MM/yyyy')} - {format(dateRange.to, 'dd/MM/yyyy')}
                      </p>
                    )}
                  </div>
                </div>

                <div className="flex items-start gap-3">
                  <FileText className="h-5 w-5 text-primary mt-0.5" />
                  <div className="flex-1">
                    <p className="text-sm font-medium">Sections</p>
                    <p className="text-sm text-slate-600">
                      {Object.values(config.sections).filter(Boolean).length} / {Object.keys(config.sections).length}
                    </p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Format d'Export</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <Button
                variant={config.format === 'pdf' ? 'default' : 'outline'}
                className="w-full justify-start"
                onClick={() => setConfig({ ...config, format: 'pdf' })}
              >
                <FileText className="mr-2 h-4 w-4" />
                PDF
              </Button>
              <Button
                variant={config.format === 'excel' ? 'default' : 'outline'}
                className="w-full justify-start"
                onClick={() => setConfig({ ...config, format: 'excel' })}
              >
                <FileSpreadsheet className="mr-2 h-4 w-4" />
                Excel
              </Button>
            </CardContent>
          </Card>

          <Card className="border-primary/20 bg-primary/5">
            <CardContent className="pt-6">
              <Button
                className="w-full"
                size="lg"
                onClick={handleGenerateReport}
                disabled={isGenerating}
              >
                {isGenerating ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Génération en cours...
                  </>
                ) : (
                  <>
                    <Download className="mr-2 h-4 w-4" />
                    Générer le Rapport
                  </>
                )}
              </Button>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <AlertCircle className="h-5 w-5 text-blue-500" />
                Information
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-slate-600">
                La génération du rapport peut prendre quelques instants. Vous recevrez une notification
                lorsqu'il sera prêt à télécharger.
              </p>
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Historique des Rapports (Future feature) */}
      <Card>
        <CardHeader>
          <CardTitle>Rapports Récents</CardTitle>
          <CardDescription>
            Accédez rapidement à vos derniers rapports générés
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="text-center py-8 text-slate-500">
            <FileText className="h-12 w-12 mx-auto mb-4 text-slate-300" />
            <p className="mb-2">Aucun rapport généré récemment</p>
            <p className="text-sm text-slate-400">
              Vos rapports générés apparaîtront ici
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
