import { apiClient } from './client'

export interface Rapport {
  id: string
  equipe: string
  createur: string
  createur_nom: string
  type: 'INDIVIDUEL' | 'EQUIPE' | 'HEBDOMADAIRE' | 'MENSUEL'
  type_display: string
  format: 'PDF' | 'EXCEL'
  format_display: string
  statut: 'EN_ATTENTE' | 'EN_COURS' | 'TERMINE' | 'ERREUR'
  statut_display: string
  dateDebut: string
  dateFin: string
  joueur?: string
  joueur_nom?: string
  sections: {
    overview?: boolean
    statistics?: boolean
    charts?: boolean
    alerts?: boolean
    wellness?: boolean
    sessions?: boolean
  }
  dateCreation: string
  dateGeneration?: string
  fichier?: string
  fichier_url?: string
  tailleFichier?: number
  taille_fichier_mb?: number
  messageErreur?: string
  nbTelechargements: number
  derniereConsultation?: string
}

export interface RapportCreate {
  equipe: string
  type: 'INDIVIDUEL' | 'EQUIPE' | 'HEBDOMADAIRE' | 'MENSUEL'
  format: 'PDF' | 'EXCEL'
  joueur?: string
  dateDebut?: string
  dateFin?: string
  periode?: '7days' | '14days' | '30days' | '90days' | 'custom'
  sections?: {
    overview?: boolean
    statistics?: boolean
    charts?: boolean
    alerts?: boolean
    wellness?: boolean
    sessions?: boolean
  }
}

export interface RapportPlanifie {
  id: string
  equipe: string
  createur: string
  createur_nom: string
  nom: string
  description?: string
  type: 'INDIVIDUEL' | 'EQUIPE' | 'HEBDOMADAIRE' | 'MENSUEL'
  type_display: string
  format: 'PDF' | 'EXCEL'
  format_display: string
  joueur?: string
  joueur_nom?: string
  sections: {
    overview?: boolean
    statistics?: boolean
    charts?: boolean
    alerts?: boolean
    wellness?: boolean
    sessions?: boolean
  }
  frequence: 'QUOTIDIEN' | 'HEBDOMADAIRE' | 'MENSUEL'
  frequence_display: string
  jourSemaine?: number
  jourMois?: number
  heure: string
  actif: boolean
  dateCreation: string
  dateModification: string
  derniereLancement?: string
  prochainLancement?: string
  destinatairesEmail: string[]
}

export interface RapportPlanifieCreate {
  equipe: string
  nom: string
  description?: string
  type: 'INDIVIDUEL' | 'EQUIPE' | 'HEBDOMADAIRE' | 'MENSUEL'
  format: 'PDF' | 'EXCEL'
  joueur?: string
  sections?: {
    overview?: boolean
    statistics?: boolean
    charts?: boolean
    alerts?: boolean
    wellness?: boolean
    sessions?: boolean
  }
  frequence: 'QUOTIDIEN' | 'HEBDOMADAIRE' | 'MENSUEL'
  jourSemaine?: number
  jourMois?: number
  heure: string
  destinatairesEmail?: string[]
}

export interface RapportStatistiques {
  total: number
  par_type: Record<string, number>
  par_statut: Record<string, number>
  total_telechargements: number
  dernier_rapport?: Rapport
}

export interface TelechargementInfo {
  url: string
  nom: string
  taille: number
  format: string
}

export const reportsService = {
  /**
   * Liste les rapports avec filtres optionnels
   */
  async getReports(params?: {
    equipe?: string
    type?: string
    statut?: string
  }): Promise<{ results: Rapport[]; count: number }> {
    const { data } = await apiClient.get<{ results: Rapport[]; count: number }>('/rapports/', { params })
    return data
  },

  /**
   * Récupère un rapport par son ID
   */
  async getReport(id: string): Promise<Rapport> {
    const { data } = await apiClient.get<Rapport>(`/rapports/${id}/`)
    return data
  },

  /**
   * Crée et lance la génération d'un nouveau rapport
   */
  async createReport(rapport: RapportCreate): Promise<Rapport> {
    const { data } = await apiClient.post<Rapport>('/rapports/', rapport)
    return data
  },

  /**
   * Supprime un rapport
   */
  async deleteReport(id: string): Promise<void> {
    await apiClient.delete(`/rapports/${id}/`)
  },

  /**
   * Télécharge un rapport terminé
   */
  async downloadReport(id: string): Promise<TelechargementInfo> {
    const { data } = await apiClient.post<TelechargementInfo>(`/rapports/${id}/telecharger/`)
    return data
  },

  /**
   * Régénère un rapport existant
   */
  async regenerateReport(id: string): Promise<Rapport> {
    const { data } = await apiClient.post<Rapport>(`/rapports/${id}/regenerer/`)
    return data
  },

  /**
   * Récupère les statistiques des rapports d'une équipe
   */
  async getStatistiques(equipeId: string): Promise<RapportStatistiques> {
    const { data } = await apiClient.get<RapportStatistiques>('/rapports/statistiques/', {
      params: { equipe: equipeId }
    })
    return data
  },

  /**
   * Liste les rapports planifiés
   */
  async getScheduledReports(params?: {
    equipe?: string
    actif?: boolean
  }): Promise<{ results: RapportPlanifie[]; count: number }> {
    const { data } = await apiClient.get<{ results: RapportPlanifie[]; count: number }>('/rapports-planifies/', { params })
    return data
  },

  /**
   * Récupère un rapport planifié par son ID
   */
  async getScheduledReport(id: string): Promise<RapportPlanifie> {
    const { data } = await apiClient.get<RapportPlanifie>(`/rapports-planifies/${id}/`)
    return data
  },

  /**
   * Crée une nouvelle planification de rapport
   */
  async createScheduledReport(rapport: RapportPlanifieCreate): Promise<RapportPlanifie> {
    const { data } = await apiClient.post<RapportPlanifie>('/rapports-planifies/', rapport)
    return data
  },

  /**
   * Met à jour un rapport planifié
   */
  async updateScheduledReport(id: string, rapport: Partial<RapportPlanifieCreate>): Promise<RapportPlanifie> {
    const { data } = await apiClient.patch<RapportPlanifie>(`/rapports-planifies/${id}/`, rapport)
    return data
  },

  /**
   * Supprime un rapport planifié
   */
  async deleteScheduledReport(id: string): Promise<void> {
    await apiClient.delete(`/rapports-planifies/${id}/`)
  },

  /**
   * Active un rapport planifié
   */
  async activateScheduledReport(id: string): Promise<RapportPlanifie> {
    const { data } = await apiClient.post<RapportPlanifie>(`/rapports-planifies/${id}/activer/`)
    return data
  },

  /**
   * Désactive un rapport planifié
   */
  async deactivateScheduledReport(id: string): Promise<RapportPlanifie> {
    const { data } = await apiClient.post<RapportPlanifie>(`/rapports-planifies/${id}/desactiver/`)
    return data
  },

  /**
   * Exécute manuellement un rapport planifié
   */
  async executeScheduledReport(id: string): Promise<Rapport> {
    const { data } = await apiClient.post<Rapport>(`/rapports-planifies/${id}/executer/`)
    return data
  },
}
